/* global jQuery */
void (function themeStart($) {
	'use strict';

	// Global variables
	const $MobileNavbar = $('#MobileNavbar');
	const $navToggle = $('#navToggle');

	// When clicking the main nav toggle button...
	$navToggle.on('click', (event) => {
		event.preventDefault();

		// Find initial nav state
		const navIsHidden = $MobileNavbar.attr('aria-hidden') === 'true';

		// Find what the current state of nav should be
		let newNavState;
		if (navIsHidden) {
			newNavState = {
				label: 'Hide Menu',
				expanded: true,
				hidden: false,
			};
		} else {
			newNavState = {
				label: 'Show Menu',
				expanded: false,
				hidden: true,
			};
		}

		// Updating new main nav state
		$navToggle.attr({
			'aria-label': newNavState.label,
			'aria-expanded': newNavState.expanded,
		});
		$MobileNavbar.attr({
			'aria-hidden': newNavState.hidden,
			'data-current-level': 0,
		});

		// Hide all open menus
		$MobileNavbar
			.find('.menu[aria-hidden="false"]')
			.attr({ 'aria-hidden': true });
	});

	$('.submenu-toggle').click((e) => {
		e.preventDefault();

		// Find the clicked link, menu level of the clicked link, and the menu that the link is linking to
		const $menuLink = $(e.target).closest('.menu-link');
		const linkLevel = $menuLink.attr('data-level');
		const linkMenuId = $menuLink.attr('data-page');
		const $activeSubMenu = $(`.menu[data-page="${linkMenuId}"]`);

		// Find the initial nav state of the linked menu
		const subMenuHidden = $activeSubMenu.attr('aria-hidden') === 'true';

		// Update the active menu level of the main nav
		$MobileNavbar.attr({ 'data-current-level': linkLevel });

		// Update the active submenu's visibility
		$activeSubMenu.attr({
			'aria-hidden': !subMenuHidden,
		});
	});
	$('.menu-link__back').click((e) => {
		e.preventDefault();

		// Find the clicked link, menu level of the clicked link, and the menu that the link is linking to
		const $menuLink = $(e.target).closest('.menu-link');
		const linkLevel = $menuLink.attr('data-level');
		const linkMenuId = $menuLink.attr('data-page');
		const $activeSubMenu = $(`.menu[data-page="${linkMenuId}"]`);

		// Find the initial nav state of the linked menu
		const subMenuHidden = $activeSubMenu.attr('aria-hidden') === 'true';

		// Update the active menu level of the main nav
		$MobileNavbar.attr({ 'data-current-level': linkLevel });

		// Update the active submenu's visibility
		$activeSubMenu.attr({
			'aria-hidden': !subMenuHidden,
		});
	});
})(jQuery);
